import { Component, Prop, Vue } from "vue-property-decorator";

import axios from "axios";
import FireUser from "@/dto/FireUser";
import { FirestoreService } from "@/services/FirestoreService";
import { auth, storage } from "@/firebaseInit";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { updateProfile } from "firebase/auth";

@Component
export default class AddressPopUp extends Vue {
  @Prop({ required: false, default: new FireUser() })
  fireUser!: FireUser;

  @Prop({ required: false, default: false })
  shipping!: boolean;

  @Prop({ required: false, default: "PERSONAL DETAILS" })
  label!: string;
  infoLoading = false;

  personalInfoFlag = false;
  selectedState: any = {};
  selectedCity: any = {};
  loadingCities = false;
  statesList = [];
  cityList = [];
  loading = false;
  updateDp = false;
  uploadDisabled = true;
  selectedImage: any = null;
  selectedContentImage = '';
  uploadLoading = false;
  async mounted() {
    await this.loadUserDetails();
    if (this.fireUser.address.state) {
      this.selectedState = {
        name: this.fireUser.address.state,
        iso2: this.fireUser.address.stateCode
      };
      this.selectedCity = {
        name: this.fireUser.address.city
      };
      await this.loadcities();
    }
    await this.loadStates();
  }

  async validateForm() {
    if (
      (this.$refs.addressPopUpForm as Vue & {
        validate: () => boolean;
      }).validate()
    ) {
      await this.updateAddress();
      this.togglePersonalInfo();
    }
  }

  async updateAddress() {
    try {
      this.loading = true;
      this.fireUser.address.state = this.selectedState.name;
      this.fireUser.address.stateCode = this.selectedState.iso2;
      this.fireUser.address.city = this.selectedCity.name;
      console.log(this.fireUser);
      await FirestoreService.setOrMergeDocument('users', this.fireUser.uid, this.fireUser, FireUser.converter);
    } catch (error) {
      console.log(error);

    } finally {
      this.loading = false;
    }
  }


  async loadStates() {
    const response = await axios.get(
      "https://api.countrystatecity.in/v1/countries/IN/states",
      {
        headers: {
          "X-CSCAPI-KEY":
            "dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=="
        }
      }
    );
    console.log(response);
    this.statesList = response.data.sort((a: any, b: any) =>
      a.name.localeCompare(b.name)
    );
  }
  async loadcities() {
    try {
      this.loadingCities = true;
      const stateCode = this.selectedState.iso2;
      console.log(this.selectedState);
      const response = await axios.get(
        `https://api.countrystatecity.in/v1/countries/IN/states/${stateCode}/cities`,
        {
          headers: {
            "X-CSCAPI-KEY":
              "dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=="
          }
        }
      );
      this.cityList = response.data.sort((a: any, b: any) =>
        a.name.localeCompare(b.name)
      );
      this.fireUser.address.state = this.selectedState.name;
      this.fireUser.address.stateCode = this.selectedState.iso2;
    } catch (error) {
      console.log(error);
    } finally {
      this.loadingCities = false;
    }
  }
  async loadcitiesOnStateChange() {
    try {
      this.loadingCities = true;
      this.selectedCity = {};
      const stateCode = this.selectedState.iso2;
      console.log(this.selectedState);
      const response = await axios.get(
        `https://api.countrystatecity.in/v1/countries/IN/states/${stateCode}/cities`,
        {
          headers: {
            "X-CSCAPI-KEY":
              "dEFGcHBta3E3dWtNWUlsZ09jbXIxSU1xS1BuN0J0Q05BS3Z1UXl6dg=="
          }
        }
      );
      this.cityList = response.data.sort((a: any, b: any) =>
        a.name.localeCompare(b.name)
      );
      this.fireUser.address.state = this.selectedState.name;
      this.fireUser.address.stateCode = this.selectedState.iso2;
    } catch (error) {
      console.log(error);
    } finally {
      this.loadingCities = false;
    }
  }
  async togglePersonalInfo() {
    if (this.fireUser.address.state) {
      this.selectedState = {
        name: this.fireUser.address.state,
        iso2: this.fireUser.address.stateCode
      };
      this.selectedCity = {
        name: this.fireUser.address.city
      };
      await this.loadcities();
    }
    this.personalInfoFlag = !this.personalInfoFlag;
  }
  async loadUserDetails() {

    try {
      this.infoLoading = true;
      const userDetails = auth.currentUser;
      if (userDetails) {
        const snapshot = await FirestoreService.getSingleDocument<FireUser>(
          "users",
          userDetails.uid,
          FireUser.converter
        );
        this.fireUser = snapshot;
      }
    } catch (error) {
      console.log(error);
    }
    finally {
      this.infoLoading = false;
    }
  }
  get addressDetails() {
    return this.fireUser.address.streetAddress + ', ' + this.fireUser.address.city + ', ' + this.fireUser.address.state + ', ' + this.fireUser.address.pinCode + ', ' + this.fireUser.address.country;
  }
  async updateDisplayPicture() {
    try {
      this.uploadLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const storageRef = ref(storage, `users/${this.fireUser.uid}/profilePicture.` + fileExtension);
      const resp = await uploadBytes(storageRef, this.selectedImage, metadata)
      const url = await getDownloadURL(resp.ref);
      this.fireUser.photoURL = url;
      await FirestoreService.setOrMergeDocument('users', this.fireUser.uid, this.fireUser, FireUser.converter);
      if (auth.currentUser) {
        await updateProfile(auth.currentUser, { photoURL: this.fireUser.photoURL });
      }
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
      this.updateDp = false
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadLoading = false;
    }
  }
  displayDpModal() {
    this.selectedImage = null
    this.selectedContentImage = ''
    this.updateDp = true;
  }

  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedContentImage = URL.createObjectURL(this.selectedImage);
    this.uploadDisabled = false;
  }
  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }

}
